<script>
import banner from '../../components/common/banner'
import footer from '../../components/common/footer'
import { getPlacePage, getSortCountryList, webSearchRoute, webTempRoute, getAllBannerInfo } from '../../api/api'
export default {
	data () {
		return {
      topBanner: [],
      testArr: [],
      currentLength: 0,
      changestatus: '',
      routeTotal: '',
      showList: [],
      routeList: [],
      showpage: '',
      totalPage: '',
      hassearch: '',
      searchPage: 0,
      isclick: true,
      isshow: true,
      loading: '加载更多',
      currentPage: 9,
      options: [{
        value: '1',
        label: '1'
      }, {
        value: '2',
        label: '2'
      }, {
        value: '3',
        label: '3'
      }, {
        value: '4',
        label: '4'
      }, {
        value: '5',
        label: '5'
      }, {
        value: '6',
        label: '6'
      }, {
        value: '7',
        label: '7'
      }, {
        value: '8',
        label: '8'
      }, {
        value: '9',
        label: '9'
      }, {
        value: '10',
        label: '10'
      }],
      value: '',
      issearch: false,
      bannerIndex: 0,
      showbox: false,
      island: '',
      island_list: ['曼克岛', '潘安岛', '歌谣岛', '康丹岛', '珍贝岛', '沁岛'],
      date: '',
      days: '',
      currentIndex: -1,
      currentIndex2: '',
      setting: {
        autoplay: true,
        height: '500px',
        arrow: 'hover',
        interval: 2000
      },
      pageData: {
        img_src: [
          require('./images/2.png'),
          require('./images/1.jpg'),
          require('./images/3.jpg')
        ]
      }
		}
	},
  mounted() {
    webTempRoute().then((res) => {
      this.showList = res.data;
      this.changestatus = 1;
      this.showList.forEach((data) => {
      if (data.template_show_status === 1) {
        for (let i = 0; i < data.route_list.length; i++) {
          if (data.route_list[i].is_carousel_banner === 1) {
            this.currentLength++;
            this.testArr.push(data.route_list[i]);
          }
        }
        this.$nextTick(() => {
            this.timer = setInterval(() => {
                this.autoPlay();
            }, 3000)
        });
      }
    });
    });
  },
	created() {
    sessionStorage.removeItem('routeId');
    document.addEventListener('click', (e) => {
       if (!this.$el.contains(e.target)) this.show = false
    });
    getSortCountryList().then((res) => {
      this.country_list = res.data;
    });
    getPlacePage().then((res) => {
      this.island_list = res.data;
    });
    getAllBannerInfo().then((res) => {
      this.topBanner = res.data;
    });
	},
	methods: {
    saveId(data) {
      sessionStorage.setItem('routeId', data.route_id);
      window.location.href = 'preview.html';
    },
    saveId2(data) {
      sessionStorage.setItem('routeId', data.banner_route_id);
      window.location.href = 'preview.html';
    },
    showhot() {
      this.currentIndex = -1;
      getPlacePage().then((res) => {
        this.island_list = res.data;
      });
    },
    loadMoreIsland() {
      if (this.isclick === false) {

      } else {
        this.loading = '加载中...';
        this.isclick = false;
        let info = {
          page: this.currentPage,
          limit: 10
        };
        setTimeout(() => {
          getPlacePage(info).then((res) => {
            if (res.data === '没有海岛信息') {
              this.loading = '没有更多海岛';
              setTimeout(() => {
                this.isshow = false;
              }, 2000);
            } else {
              this.isclick = true;
              this.island_list = this.island_list.concat(res.data);
              this.loading = '加载更多';
              this.currentPage += 9;
            }
          });
        }, 1000);
      }
    },
    initit() {
      this.issearch = false;
      this.bannerIndex = 0;
    },
    change(index2) {
      this.bannerIndex = index2;
    },
    autoPlay() {
      this.bannerIndex++
      if (this.bannerIndex > this.currentLength - 1) {
          this.bannerIndex = 0
      }
    },
    go() {
    this.timer = setInterval(() => {
        this.autoPlay();
      }, 5000)
    },
    stop() {
        clearInterval(this.timer);
        this.timer = null;
    },
    handleCurrentChange(val) {
      let info = {
        search: this.island,
        day: this.days,
        page: (val - 1) * 5
      };
      if (this.date !== '') {
        info.date = this.formatDate(this.date);
      } else {
        info.date = '';
      }
      webSearchRoute(info).then((res) => {
        if (res.data === '没有线路信息') {
          this.showbox = false;
          this.issearch = true;
          this.hassearch = false;
        } else {
          this.totalPage = res.data.route_count_ceil;
          if (this.totalPage <= 1) {
            this.showpage = false;
          } else {
            this.showpage = true;
          }
          this.routeTotal = res.data.route_count;
          this.routeList = res.data.route_list;
          this.showbox = false;
          this.issearch = true;
          this.hassearch = true;
        }
      });
    },
    formatDate(date) {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? '0' + m : m;
      var d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      return y + '-' + m + '-' + d;
    },
    submitsearch() {
      if (this.island === '' && this.date === '' && this.days === '') {
        this.$message('请输入搜索内容');
      } else {
        let info = {
          search: this.island,
          day: this.days,
          page: this.searchPage
        };
        if (this.date !== '') {
          info.date = this.formatDate(this.date);
        } else {
          info.date = '';
        }
        webSearchRoute(info).then((res) => {
          if (res.data === '没有线路信息') {
            this.showbox = false;
            this.issearch = true;
            this.hassearch = false;
          } else {
            this.totalPage = res.data.route_count_ceil;
            if (this.totalPage <= 1) {
              this.showpage = false;
            } else {
              this.showpage = true;
            }
            this.routeTotal = res.data.route_count;
            this.routeList = res.data.route_list;
            this.showbox = false;
            this.issearch = true;
            this.hassearch = true;
          }
        });
      }
    },
    init() {
      this.currentIndex = -1;
      this.currentIndex2 = '';
    },
    showit() {
      this.showbox = true;
      let distance = document.getElementById('example').offsetTop;
      window.scrollTo(0, distance - 140);
    },
    hideit() {
      this.showbox = false;
    },
    chooseit(item, index) {
      this.currentIndex = index;
      let info = {
        country_id: item.id
      };
      getPlacePage(info).then((res) => {
        this.island_list = res.data;
      });
    },
    chooseit2(item, index) {
      this.currentIndex2 = index;
      this.island = item.place_name;
      this.showbox = false;
    }
	},
	components: {
		'cheeru-banner': banner,
		'cheeru-footer': footer
	}
}
</script>

